<template>
<div>
      <el-menu class="el-menu-demo" mode="horizontal" @select="handleSelect" 
        background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b">
  <el-row justify="space-around" type="flex">
    <el-menu-item index="1">首页</el-menu-item>
      <router-link to="Taglist"><el-menu-item index="3" >标签列表</el-menu-item></router-link>
      <router-link to="/create"><el-menu-item index="8" >发布资料</el-menu-item></router-link>
    <el-menu-item index="4">个人空间</el-menu-item>
    <el-row justify="space-around" type="flex" :gutter="2">
      <el-menu-item index="5" >
        <el-col :span="100">
          <el-input v-model="input" placeholder="搜索你想要的资料">
          </el-input>
      </el-col>
      </el-menu-item>
      <el-menu-item index="6" >
       <el-col :span="2">
          <div class="grid-content bg-purple">
            <router-link to="Result">
              <el-button>search</el-button>
           </router-link>
         </div>
       </el-col>
      </el-menu-item>
    </el-row>
  </el-row>
</el-menu>

  </div>
</template>

<script>
export default {
  name: 'Home',
  data(){
    return{
      activeIndex: '1',
    }
  },
  methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
        if (key == '4') {
            this.$router.push({path: '/profile', query: {uid: 1}});

        }
      }
  }
}
</script>

<style>
h1{
  font-weight: normal;
}
a{
  color: #42b983
}
</style>



